import { Sprite, Graphics, Texture, Point } from "pixi.js";
import { TweenMax } from "gsap";
import type { Container, CanvasRenderer, WebGLRenderer } from "pixi.js";

import * as fgs from "../../fgs";

export class LoadingView extends fgs.LoadingView {
	public logoWidth: number = 1024;
	private _logo: Container = null;
	private _logoOriginalPosition: Point;
	private _logoOutline: Sprite = null;
	private _logoFill: Sprite = null;
	private _logoMask: Graphics = null;

	/**
	 * On load view
	 */
	public onLoad(): void {
		this._logoFill = new Sprite(Texture.fromImage("quick-logo-fill.svg"));
		this._logo.addChild(this._logoFill);

		this._logoOutline = new Sprite(Texture.fromImage("quick-logo-outline.svg"));
		this._logo.addChild(this._logoOutline);

		this._logoOutline.scaleXY = this._logoFill.scaleXY = 2;

		this._logoMask = new Graphics();
		// due to a bug in graphics i have to draw something in another color first
		this._logoMask.beginFill(0x00ff00);
		this._logoMask.drawRect(0, 0, 1, 1);
		this._logoMask.endFill();
		// then i can draw my shape
		this._logoMask.beginFill(0x00ff00);
		this._logoMask.drawRect(0, 0, this._logoFill.width, this._logoFill.height);
		this._logoMask.y = this._logoFill.height;
		this._logoMask.endFill();

		this._logoFill.mask = this._logoMask;
		this._logoOriginalPosition = new Point(this._logo.docking.x, this._logo.docking.y);
		this._logo.docking.x = 0;
		this._logo.docking.y = 0;
		this._logo.pivot.set(this._logoFill.width * 0.5, this._logoFill.height * 0.5);
		this._logo.addChild(this._logoMask);
	}

	/**
	 * override renderWebGL to capture view size and perform resize
	 * @param renderer the WebGLRenderer
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		this._onResizeHandler(renderer);
	}

	/**
	 * override renderCanvas to capture view size and perform resize
	 * @param renderer the CanvasRenderer
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		this._onResizeHandler(renderer);
	}

	private _onResizeHandler(renderer: CanvasRenderer | WebGLRenderer): void {
		this._logo.dockingBounds.width =
			(this._logoFill.width + this._logoOriginalPosition.x) / renderer.resolution;
		this._logo.dockingBounds.height =
			(this._logoFill.height + this._logoOriginalPosition.y) / renderer.resolution;

		this.dockingBounds.width =
			this.background.dockingBounds.width =
			this.background.width =
				renderer.width; // / renderer.resolution;
		this.dockingBounds.height =
			this.background.dockingBounds.height =
			this.background.height =
				renderer.height; // / renderer.resolution;
	}

	/**
	 * Defines the function called each time a file is loaded
	 * @param percentage the percentage of files already loaded
	 */
	public onProgress(percentage: number): void {
		const progressRatio = 1 - percentage / 100;
		TweenMax.to(this._logoMask, 0.4, { y: this._logoFill.height * progressRatio });
	}

	/**
	 * On the view will disappear
	 */
	public onWillDisappear(): void {
		this.visible = false;
	}
}
