import * as fgs from "../../fgs";
import { EVisualIdentityEvent } from "../../../events";

export class InGameView extends fgs.InGameView {
	/**
	 * Called once the view is loaded
	 */
	public onLoad(): void {
		super.onLoad();

		this.vi.emitter.on(EVisualIdentityEvent.AboutGame, () => {
			this.vi.game.com.bridge.executeAction("GAME_RULES");
		});

		this.vi.emitter.on(EVisualIdentityEvent.ShowMainRules, () => {
			this.vi.game.com.bridge.executeAction("GLOBAL_RULES");
		});

		// EVisualIdentityEvent.ShowWinningTable will be handled in game
	}
}
